@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400,700&display=swap&subset=latin-ext);
@import url(https://fonts.googleapis.com/css?family=VT323&display=swap&subset=latin-ext);
@import url(https://fonts.googleapis.com/css?family=Oswald:400,700&display=swap&subset=latin-ext);
html {
  font-family: "Roboto Condensed", sans-serif; }

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

.header {
  align-items: center;
  height: 7rem;
  display: flex;
  flex-direction: row;
  background: -webkit-gradient(linear, left top, right top, from(#2a0045), to(#6441a5));
  background: linear-gradient(to right, #2a0045, #6441a5); }
  .header__img {
    height: 80%;
    margin: 0 0 0 10%;
    -webkit-transition: 0.3s linear;
    transition: 0.3s linear; }
    .header__img:hover {
      -webkit-animation: shakes 0.2s ease-in-out infinite alternate;
              animation: shakes 0.2s ease-in-out infinite alternate; }
  .header__paragraph {
    margin: 0 0 0 2rem;
    color: whitesmoke;
    font-size: 3rem; }

.memes {
  text-align: center;
  padding: 2rem; }
  .memes__top-text, .memes__bottom-text {
    color: green;
    font-weight: bold;
    font-size: 2rem;
    letter-spacing: 0.2rem;
    text-transform: uppercase; }
  .memes__img {
    width: 31rem;
    height: 21rem; }

.memes__top-text {
  position: absolute;
  top: 30%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%); }

.memes__bottom-text {
  position: absolute;
  top: 72%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%); }

.meme-form {
  width: 80%;
  margin: 1.5rem 1rem 0 1rem;
  display: flex;
  justify-content: space-between; }
  .meme-form__input {
    font-size: 1.5rem;
    height: 2rem;
    text-align: center;
    width: 40%; }
    .meme-form__input:focus {
      outline: none;
      background: #d8f3df;
      border: 2px solid green; }
  .meme-form__button {
    border: none;
    font-size: 1.5rem;
    letter-spacing: 0.2rem;
    color: whitesmoke;
    background: #6441a5; }

.footer {
  background: black;
  color: #56e39f;
  font-size: 1.4rem;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  margin: 0;
  padding: 1.4rem;
  text-align: center;
  text-transform: uppercase; }

@-webkit-keyframes shakes {
  0% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg); }
  100% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg); } }

@keyframes shakes {
  0% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg); }
  100% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg); } }

